import React, { Component } from "react";
import { Card, CardContent, Typography, withStyles } from "@material-ui/core";
import { toAbsoluteUrl } from "../../../app/utils/UtilMethods";
import ZoomImage from "../../../shared/components/ZoomImage";

const styles = (theme) => ({
  cardWrapper: {
    maxWidth: "550px",
  },
  image: {
    maxWidth: "100%",
    maxHeight: "100%",
    verticalAlign: "middle",
    borderRadius: "20px",
    boxShadow: theme.shadows[4],
  },
});

class CardWithImage extends Component {
  render() {
    const {
      classes = {},
      imageSrc = undefined,
      headline = undefined,
      text = undefined,
      children = undefined,
    } = this.props;
    return (
      <Card className={classes.cardWrapper}>
        {imageSrc && (
          <ZoomImage
            src={toAbsoluteUrl(imageSrc)}
            alt={"Image Alt"}
            className={classes.image}
          />
        )}
        <CardContent
          className={
            "sm-p-top text-align-center align-items-center justify-content-center"
          }
          style={{ backgroundColor: "#c7dcde" }}
        >
          <Typography
            gutterBottom
            variant="h5"
            component="h3"
            className={
              "text-align-center align-items-center justify-content-center"
            }
          >
            {headline}
          </Typography>
          {text && (
            <Typography
              variant="body2"
              color="textSecondary"
              component="p"
              className={
                "text-align-center align-items-center justify-content-center"
              }
            >
              {text}
            </Typography>
          )}
          {children}
        </CardContent>
      </Card>
    );
  }
}
export default withStyles(styles, { withTheme: true })(CardWithImage);
